<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">数据提取</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">提取数据申请</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>
          申请信息
        <div class="button-content">
          <Button type="primary" @click="applyBtn" v-authcontrol="{menuCode:this.$route.query.menuCode == '107001007000'?'107001007010':'107003000101'}" v-if="firstLoginFlag">重新发起申请</Button>
          <Button type="primary" @click="handleSubmit('formValidate')" v-authcontrol="{menuCode:this.$route.query.menuCode == '107001007000'?'107001007020':'107003000102'}" v-if="!disabledFlag && firstLoginFlag">重新提交审核</Button>
        </div>
        <div class="stepBox">
          <div class="stepBoxCell" v-for="(item,index) in stepBox" :key="item.nodeName">
            <div :class="item.nodeId>workFlowState?'stepBoxCell-1':'stepBoxCell-1-2'">{{index+1}}</div>
            <div class="stepBoxCell-2">{{item.nodeName}}</div>
            <div class="stepBoxCell-3" v-if="stepBoxCell3(index)"></div>
          </div>
        </div>
      </div>
      <div class="contentCellBody1">
        <Form
          ref="formValidate"
          :label-width="120"
          :rules="ruleValidate"
          :model="formValidate"
          inline
        >
          <Row>
            <FormItem label="提取数据期间：" prop="extraTime">
              <DatePicker
                split-panels
                type="daterange"
                placeholder="请选择时间"
                :readonly="disabledFlag"
                :value="formValidate.extraTime"
                @on-change="formValidate.extraTime=$event"
              ></DatePicker>
            </FormItem>
            <FormItem label="提取机构：" prop="extraUnit">
              <treeselect
                v-model="formValidate.extraUnit"
                :disabled="disabledFlag"
                :options="data2"
                placeholder="请选择机构"
                style="width:160px"
              />
            </FormItem>
            <FormItem label="提交截止日期：" prop="deadLine">
              <DatePicker
                type="date"
                placeholder="请选择时间"
                :readonly="disabledFlag"
                :value="formValidate.deadLine"
                @on-change="formValidate.deadLine=$event"
              ></DatePicker>
            </FormItem>
          </Row>
          <Row>
            <FormItem label="权限使用时间：" prop="permissionTime">
              <DatePicker
                type="daterange"
                :readonly="disabledFlag"
                placeholder="请选择时间"
                :value="formValidate.permissionTime"
                @on-change="formValidate.permissionTime=$event"
              ></DatePicker>
            </FormItem>
            <FormItem label="紧急程度：" prop="emergeLevel">
              <RadioGroup v-model="formValidate.emergeLevel">
                <Radio
                  v-for="item in emergeLevels"
                  :disabled="disabledFlag"
                  :label="item.code"
                  :key="item.code"
                >{{item.name}}</Radio>
              </RadioGroup>
            </FormItem>
            <FormItem label="流程标题："  prop= "bpmTitle">
                <Input v-model="formValidate.bpmTitle"  style="width:185px" :readonly="disabledFlag"></Input>
            </FormItem>
          </Row>
          <!-- <Row>
            <FormItem label="备注：" class="form-item" style="width:100%">
              <Input
                v-model="formValidate.notes"
                :disabled="disabledFlag"
                :autosize="{minRows: 2,maxRows: 5}"
                type="textarea"
                style="width:100%;"
              />
            </FormItem>
          </Row> -->
          <Row>
            <Form-item label="附件：" prop="filePath" class="form-item" style="width:100%">
              <Upload
                ref="upload"
                multiple
                type="drag"
                :show-upload-list="false"
                :disabled="disabledFlag"
                :default-file-list="defaultList"
                :before-upload="handleUploadfile"
                :format="['zip','rar','xls','xlsx','doc','docx','pdf']"
                :max-size="10240"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                :action="axios.defaults.baseURL+'/system/remote/micUpload'"
                :on-success="handleSuccess"
                :on-error="handleError"
              >
                <div style="padding: 20px 0">
                  <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                  <p>请上传附件</p>
                </div>
              </Upload>
              <div>上传格式为zip、rar、xls、xlsx、doc、docx、pdf，且大小不超过10MB</div>
            </Form-item>
          </Row>
          <Form-item label="" class="form-item" style="width:100%">
          <Table border :columns="columns1" :data="uploadList"></Table>
          </Form-item>
           <Row>
            <FormItem label="备注：" class="form-item" style="width:100%">
              <Input
                v-model="formValidate.notes"
                :disabled="disabledFlag"
                :autosize="{minRows: 2,maxRows: 5}"
                type="textarea"
                style="width:100%;"
              />
            </FormItem>
          </Row>
        </Form>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>数据列表
      </div>
      <div class="contentCellBody downBox">
        <Row style="margin-left:1px;margin-bottom:20px">
          <ButtonGroup>
            <Button
              type="primary"
              @click="replayBatch()"
              v-if="formValidate.workFlowState=='06' &&  new Date().getTime()<new Date(formValidate.permissionTime2).getTime() && this.num >= 16"
            >重新执行</Button>
          </ButtonGroup>
          <!-- <ButtonGroup>
            <Button
              type="primary"
              @click="batchDownload"
              v-if="formValidate.workFlowState=='06'"
            >批量下载</Button>
          </ButtonGroup>
          <Select v-model="model6" style="width:80px" v-if="formValidate.workFlowState=='06'">
            <Option value="EXCEL" disabled>EXCEL</Option>
            <Option value="MYSQL" disabled>MYSQL</Option>
            <Option value="TXT">TXT</Option>
            <Option value="CSV">CSV</Option>
          </Select>-->
        </Row>
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td>{{tableHeader.title1}}</td>
                <td class="textLeft">{{tableHeader.title2}}</td>
                <td>{{tableHeader.title3}}</td>
                <td>{{tableHeader.title4}}</td>
                <td>{{tableHeader.title5}}</td>
                <td>{{tableHeader.title6}}</td>
                <td>{{tableHeader.title7}}</td>
                <td class="textLeft">{{tableHeader.title8}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBody" :key="item.id">
                <td>
                  <Checkbox v-model="item.flag"></Checkbox>
                  {{index+1}}
                </td>
                <td class="textLeft">{{item.tableName}}</td>
                <td
                  style="color:RGB(91,149,240); cursor: pointer;"
                  @click="successfun(item,item.succNum,item.errorNum)"
                >{{item.execuStatus=='运行结束'?item.succNum:''}}</td>
                <td
                  style="color:RGB(91,149,240);cursor: pointer;"
                  @click="errorfun(item,item.succNum,item.errorNum)"
                >{{item.execuStatus=='运行结束'?item.errorNum:''}}</td>
                <td
                  v-if="item.execuStatus=='运行失败'"
                  style="color:RGB(250,174,98)"
                >{{item.execuStatus}}</td>
                <td
                  v-if="item.execuStatus=='运行结束'"
                  style="color:RGB(56,218,167)"
                >{{item.execuStatus}}</td>
                <td
                  v-if="item.execuStatus=='运行中'"
                  style="color:RGB(230,230,230)"
                >{{item.execuStatus}}</td>
                <td
                  v-if="item.execuStatus=='等待运行'"
                  style="color:RGB(230,230,230)"
                >{{item.execuStatus}}</td>
                <td>{{item.execuStartTime?item.execuStartTime.substr(0,19):''}}</td>
                <td>{{item.execuEndTime?item.execuEndTime.substr(0,19):''}}</td>
                <td class="textLeft">
                  <span
                    class="operationSpan"
                    @click="singleDownload(item)"
                    v-if="item.execuStatus=='运行结束'"
                  >下载</span>
                </td>
                  <!-- <td class="textLeft">
                  <span
                    class="operationSpan"
                    @click="singleDownload(item)"
                  
                  >下载</span> -->
                </td>
              </tr>
            </tbody>
          </table>
        </Row>
      </div>
    </div>
    <div style="width:100%;height:80px">如执行失败，请点击重新执行按钮，如反复失败请联系运维人员 0123-XXXXXXXX</div>
        <!-- ============== -->
    <Modal :mask-closable="false"   v-model="watchAppenModel" width="99%" title="查看">
      <div class="content">
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td v-for="(item,index) in tableHeaderAppend">{{item.val}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBodyAppend">
                <td>{{index+1}}</td>
                <td>{{item.tableName}}</td>
                <td>{{item.sourceTableValue}}</td>
                <td><span class="spanBox" @click="singleDownload2(item)" v-if="item.execuStatus!='运行失败' || item.execuStatus!='运行中'" >下载</span></td>
              </tr>
            </tbody>
          </table>
        </Row>
      </div>
    </Modal>
    <!-- ===========弹框 -->
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect },
  created() {
    let that = this;
    let obj = this.$route.query;
    if (obj.disabledFlag == "false" || obj.disabledFlag == false)
    this.disabledFlag = true;
    this.axios
      .post(`/extract/pageReviewDetail?bathcId=${obj.batchId}&Id=${obj.Id}`)
      .then(response => {
        if (response.code == "200") {
          let data0 = response.data.basicData;
          let data1 = response.data.recordDatas;
          data0.bpmTitle = data0.taskName;
          data0.fileName = [];
          data0.filePath = [];
          that.formValidate = data0;
          //that.formValidate.bpmTitle = that.formValidate.taskName;
          that.workFlowState = data0.workFlowState;
          let temp = 0;
          for(var i = 0;i<data1.length;i++){
              if (data1[i].execuStatus=="执行成功"||data1[i].execuStatus=="运行结束"|| data1[i].execuStatus=="运行失败") temp++;
          }
          debugger
          this.num=temp;
          that.axios
            // .get(
            //   `/bpm/getNodeDefDetailByPerson?username=${data0.extraApplicant}&bpmType=extract`
            // )
            
            .post(
              `/bpm/getDefNodeName?bpm_name=${obj.bpmName}`
            )
            .then(function(re) {
              debugger
              that.stepBox = re.data;
              that.formValidate.bpmName = re.data[1].bpmName;
            })
            .catch(function(error) {});
            for (let i = 0; i < that.formValidate.fileList.length; i++) {
              that.defaultList.push({
                url: that.formValidate.fileList[i].fileId,
                name: that.formValidate.fileList[i].filename
              });
            }
          that.formValidate.extraTime = new Array(
            that.formValidate.extraTime,
            that.formValidate.extraTime2
          );
          that.formValidate.permissionTime = new Array(
            that.formValidate.permissionTime,
            that.formValidate.permissionTime2
          );
          that.tableBody = data1;
          for (let i = 0; i < that.tableBody.length; i++) {
            that.$set(that.tableBody[i], "flag", false);
          }
        }
      });
    this.axios
      .get("/extract/getExtractDic/EMERCRGENCY_LEVEL,BUSI_SCOPE")
      .then(function(response) {
        if (response.code == "200") {
          that.emergeLevels = response.data.EMERCRGENCY_LEVEL;
          that.busiScopes = response.data.BUSI_SCOPE;
        }
      })
      .catch(function(error) {});
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data2 = response.data;
        }
      })
      .catch(error => {});
  },
  watch: {
    "$store.state.firstLoginFlag": {
      handler(newVal) {
        this.firstLoginFlag = newVal;
      }
    }
  },
  data() {
    const validaterFile = (rule, value, callback) => {
      if (this.uploadList.length == 0) {
        return callback(new Error("请上传附件"));
      } else {
        callback();
      }
    };
    const validaterExtraTime = (rule, value, callback) => {
      if (this.formValidate.extraTime[0] == "") {
        return callback(new Error("请选择时间"));
      } else {
        callback();
      }
    };
    const validaterExtraUnit = (rule, value, callback) => {
      if (this.formValidate.extraUnit == undefined) {
        return callback(new Error("请选择提取机构"));
      } else {
        callback();
      }
    };
    const validaterPermissionTime = (rule, value, callback) => {
      if (this.formValidate.permissionTime[0] == "") {
        return callback(new Error("请选择时间"));
      } else {
        callback();
      }
    };
    const validaterNextUserInfo = (rule, value, callback) => {
      if (this.formValidate.nextUserInfo == "" && this.modal1) {
        return callback(new Error("请至少选择一个审核人"));
      } else {
        callback();
      }
    };
    return {
      firstLoginFlag: this.$store.state.firstLoginFlag,
      num: 0,
      watchAppenModel:false,
      tableHeaderAppend:[
        {val:'序',},
        {val:'分片检查表名称'},
        {val:'检查表名称'},
        {val:'操作'},
      ],
      tableBodyAppend:[
     
      ],
      options1: {
        disabledDate(date) {
          return date && date.valueOf() < Date.now() - 24 * 60 * 60 * 1000;
        }
      },
      defaultList: [],
      columns1: [
        {
          title: "文件名",
          key: "name"
        },
        {
          title: "操作",
          key: "",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.fileDownload(params.index, params.row.name);
                    }
                  }
                },
                "下载"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    disabled: this.disabledFlag,
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.remove(params.index);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ],
      uploadList: [],
      disabledFlag: false,
      model6: "TXT",
      workFlowState: "01",
      stepBox: [],
      tableHeader: {
        title1: "序",
        title2: "检查表名称",
        title3: "成功数据量",
        title4: "错误数据量",
        title5: "执行状态",
        title6: "执行开始时间",
        title7: "执行结束时间",
        title8: "操作"
      },
      tableBody: [],
      data2: [],
      formValidate: {
        fileName: [],
        filePath: [],
        nextUserInfo: "",
        extraTime: ["", ""], //提取数据时间
        extraUnit: null, //机构
        permissionTime: ["", ""], //期限时间
        emergeLevel: "", //紧急
        extraApplicant: "lihl",
        deadLine: "", //提交截止日期
        notes: "" ,//备注
        bpmTitle:""
        // -----
      },
      busiScopes: [],
      emergeLevels: [],
      statusList: [
        { code: "0", name: "等待运行" },
        { code: "1", name: "运行中" },
        { code: "2", name: "运行结束" },
        { code: "3", name: "运行失败" }
      ],
      ruleValidate: {
        filePath: [
          {
            required: true,
            validator: validaterFile,
            trigger: "change"
          }
        ],
         bpmTitle: [
          {
            required: true,
            message: "请输入主题",
            trigger: "blur"
          },
          {
            type: "string",
            max: 50,
            message: "最大输入长度不能超过50字",
            trigger: "blur"
          }
        ],
        nextUserInfo: [
          {
            required: false,
            validator: validaterNextUserInfo,
            trigger: "on-check-change"
          }
        ],
        notes: [
          {
            type: "string",
            max: 128,
            message: "长度最大不能超过128",
            trigger: "blur"
          }
        ],
        extraUnit: [
          {
            required: true,
            message: "请选择机构",
            validator: validaterExtraUnit,
            trigger: "xxxx"
          }
        ],
        emergeLevel: [
          { required: true, message: "请选择紧急程度", trigger: "change" }
        ],
        extraTime: [
          {
            required: true,
            message: "请选择时间",
            validator: validaterExtraTime,
            trigger: "xxxx"
          }
        ],
        deadLine: [
          {
            required: true,
            message: "请选择时间",
            trigger: "change"
          }
        ],
        permissionTime: [
          {
            required: true,
            validator: validaterPermissionTime,
            message: "请选择时间",
            trigger: "xxxx"
          }
        ]
      }
    };
  },
  beforeUpdate() {
    this.uploadList = this.$refs.upload.fileList;
  },
  methods: {
    handleFormatError(file) {
      this.$Notice.warning({
        title: "附件格式错误",
        desc: "请选择zip、rar、xls、xlsx、doc、docx、pdf."
      });
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: "超出了附件大小限制",
        desc: " 不应超过10M"
      });
    },
    updateNextUserInfo(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidate.nextUserInfo = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.formValidate.nextUserInfo = element.code;
      });
      this.$refs["formValidate"].validateField("nextUserInfo");
    },
    selectTreeCleanUp(treeDatas) {
        for (let i = 0; i < treeDatas.length; i++) {
            if (treeDatas[i].children && treeDatas[i].children.length >0) {
                this.selectTreeCleanUp(treeDatas[i].children);
            }
            else this.$delete(treeDatas[i], "children");
        }
    },
    handleUploadfile(file) {
      const check = this.uploadList.length < 5;
      if (!check) {
        this.$Notice.warning({
          title: "只能上传最多五个附件."
        });
      }
      return check;
    },
    handleSuccess(response, file) {
      if (response.code == "200") {
        file.url = response.data[0].fileId;
        this.$refs["formValidate"].validateField("filePath");
      }
    },
    handleError() {
      this.$Message.error("上传失败");
    },
    singleDownload(item) {
     
         this.axios
         .post(`/extract/idenDownExtractFile?tableName=${item.tableField}&batchId=${item.batchId}`)
        .then(response => {
           console.log(response);
          if(response.code=='201'){
             this.tableBodyAppend=response.data;
             this.watchAppenModel=true;          
          }else {
            
             this.axios
              .post(`/extract/downExtractFile?tableName=${item.tableField}&batchId=${item.batchId}`, {}, {
                  responseType: 'blob'
              })
              .then(response => {
                 if(response.code=='201'){
                   
                 }else{
                    this.download(response, item.tableName + ".zip");
                 }
              })
              .catch(error => {});
          }
        })
        .catch(error => {});

     
    },
    singleDownload2(item){
         this.axios
              .post(`/extract/downAllExtractFile?sourceTableName=${item.sourceTableName}&tableName=${item.tableName}&batchId=${item.instanceId}`, {}, {
                  responseType: 'blob'
              })
              .then(response => {
                 if(response.code=='200'){
                    
                 }else{
                    this.download(response, item.tableName + ".zip");
                 }
        })
        .catch(error => {});
    },
    batchDownload() {
      let that = this;
      let selectFiles = [];
      this.tableBody.forEach(element => {
        if (element.flag) {
          selectFiles.push({ id: element.id, tableName: element.tableName });
        }
      });
      if (selectFiles.length == 0) {
        this.$Message.warning("至少选择一条");
      } else {
        selectFiles.forEach(e => {
          let data = JSON.stringify({
            fileType: this.model6,
            id: e.id
          });
          this.axios
            .post("/extract/opt/downloadFile", data, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(response => {
              this.download(response, e.tableName + ".zip");
            })
            .catch(error => {});
        });
      }
    },
    stepBoxCell3(index) {
      let index2 = index + 1;
      if (index2 == this.stepBox.length) {
        return false;
      } else {
        return true;
      }
    },
    handleSubmit(name) {
      let that = this;
      this.$refs[name].validate(valid => {
        if (valid) {
          that.uploadList.forEach(element => {
            that.formValidate.fileName.push(element.name);
            that.formValidate.filePath.push(element.url);
          });
          let data = JSON.parse(JSON.stringify(that.formValidate));
          data.fileName = JSON.stringify(data.fileName);
          data.filePath = JSON.stringify(data.filePath);
          data.extraTime = JSON.stringify(data.extraTime);
          data.permissionTime = JSON.stringify(data.permissionTime);
          data = JSON.stringify(data);
          this.axios
            .post("/extract/task/doRejectSubmitRetry", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                that.$router.push({
                  name: "dataExtractionApplyWindow",
                });
              } else {
                that.$Message.error(response.message);
              }
            })
            .catch(error => {});
        }
      });
    },
    replayBatch() {
      let that = this;
      let batchId = "";
      batchId = this.tableBody[0].batchId;
      this.axios
        .post(`/extract/reDoExtractData/batchId=${batchId}`)
        .then(res => {
          that.$Message.success("已经重新执行，请稍等");
        })
        .catch(error => {});
    },
   remove(index){
      let filePath = this.uploadList[index].url;
      this.axios
      .post(`/system/remote/delFile/${filePath}`)
      .then(response => {
          if ((response = true)) {
              this.uploadList.splice(index, 1);
              this.$Message.success("删除成功");
          }else {
              this.$Message.error("删除失败");
          }
      })
    },
    fileDownload(index, fileName) {
      let that = this;
      let filePath = this.uploadList[index].url;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    applyBtn() {
      this.$router.push({
        name: "dataExtractionApply",
        query: {
          obj: this.formValidate
        }
      });
    },
    successfun(item, succNum, errorNum) {
      if(succNum==''||item.execuStatus!='运行结束'){
        return false;
      }else{
        this.$router.push({
          name: "dataExtractionPreview",
          query: {
            type: "success",
            batchId: item.batchId,
            succNum: succNum,
            errorNum: errorNum,
            isSuc: "0",
            reDoNum: item.execuApplicationNew,
            tableName: item.tableField
          }
        });
      }
    },
    errorfun(item, succNum, errorNum) {
      if(succNum==''||item.execuStatus!='运行结束'){
        return false;
      }else{
        this.$router.push({
          name: "dataExtractionPreview",
          query: {
            type: "error",
            batchId: item.batchId,
            succNum: succNum,
            errorNum: errorNum,
            isSuc: "1",
            reDoNum: item.execuApplicationNew,
            tableName: item.tableField
          }
        });
      }
    }
  }
};
</script>
<style scoped>
.downBox >>> .ivu-select-selection {
  background-color: #2d8cf0;
  color: white;
}
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.fujian {
  padding-left: 122px;
  position: relative;
}
.fujian-title {
  position: absolute;
  left: 73px;
}
.fujianself {
  width: 250px;
  height: 20px;
  border-radius: 4px;
  padding: 2px 20px;
  background-color: RGB(238, 243, 248);
  cursor: pointer;
}
.operationSpan {
  color: blue;
  margin: 0 5px;
  cursor: pointer;
}
</style>


